@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.wrapper{background:black;padding:30px 100px;min-height:100vh}@media screen and (max-width: 600px){.wrapper{padding:30px}}.header-row{padding:15px;text-align:center;background-color:#d3d3d3;margin-bottom:10px}@media screen and (max-width: 600px){.header-row{background-color:white}}.row-panel{text-align:center}.row-panel .row{text-align:center;margin:0 10%}@media screen and (max-width: 600px){.row-panel .row{margin:5%}}.data-panel{margin:0px 30px;text-align:center;color:#d8d8d8}.data-panel .header-row{padding:15px;text-align:center;background-color:#d3d3d3;margin-bottom:10px;color:black;margin:30px 0px}@media screen and (max-width: 600px){.data-panel .header-row{background-color:white}}.data-panel .column{float:left;width:50%;padding:10px;border:10px}.data-panel .column-4{text-align:center;padding:2.5%;margin:5%;width:40%;border:1px solid #b4b4b4;text-transform:wrap}.data-panel .chart{margin:0px 20%}.data-panel .row:after{content:"";display:table;clear:both}.data-panel .header{font-weight:600;font-size:28px;text-align:center}.data-panel .title{padding-bottom:0px}.data-panel .title span{font-size:28px;font-weight:600;align-items:center}@media screen and (max-width: 600px){.data-panel .column,.data-panel .column-4{width:100%}.data-panel .chart{margin:0px}}.chart-modal{color:white;background-color:black;text-align:center;border:1px solid #b4b4b4}.chart-modal .close{color:#d8d8d8;background-color:black}.chart-modal .close:hover{color:#ffffff}.btn{background-color:#d8d8d8;border:0px;color:black;border-radius:10px;transition:0.4s;border:0}.btn:hover{background-color:#00ccff81;color:#ffffff;border-radius:30px}.fixed-row{padding:20px;min-height:350px}.margin-top-lg{margin-top:10%}@media screen and (max-width: 600px){.margin-top-lg{margin-top:20px}}.wrapper-box{padding:15px}

